<template>
  <div class="answer">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>

    <van-row :gutter="20" class="info" v-if="GotData">
      <van-col span="24" class="info-item"> {{`${data.universityInfo.name}(${data.universityInfo.code})`}}</van-col>
      <van-col span="24" class="info-item"> {{data.universityInfo.userName}}</van-col>
      <van-col span="24" class="info-item" v-if="data.universityInfo.cardNo">{{data.universityInfo.cardNo}}</van-col>
      <van-col span="24" class="info-item"> {{data.universityInfo.score}}</van-col>
      <van-col span="24" class="info-item"> {{data.universityInfo.userPosition}}</van-col>
      <van-col span="24" class="info-item item-tip">20182017
      </van-col>

      <van-col span="24" class="info-item"> {{data.universityInfo.level}}</van-col>
      <van-col span="24" class="info-item"> {{data.universityInfo.province}}</van-col>
      <van-col span="24" class="info-item"> {{data.universityInfo.phone}}</van-col>
      <van-col span="24" class="info-item">
        <span class="item-blue">{{data.universityInfo.website}}</span>
      </van-col>
    </van-row>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row :gutter="20" class="info" v-if="GotData">

      <van-col span="24" class="info-item" v-if="data.universityInfo.rate.all"> {{data.universityInfo.rate.all.toString().includes('-') ? '-' : (data.universityInfo.all.rate * 100).toFixed(2) + '%'}}</van-col>
      <van-col span="24" class="info-item" v-if="data.universityInfo.rate.first">{{data.universityInfo.rate.first.toString().includes('-') ? '-' : (data.universityInfo.rate.first * 100).toFixed(2)+ '%' }}</van-col>
      <van-col span="24" class="info-item" v-if="data.universityInfo.rate.second">{{data.universityInfo.rate.second.toString().includes('-') ? '-' : (data.universityInfo.rate.second * 100).toFixed(2)+ '%' }}</van-col>
      <van-col span="24" class="info-item" v-if="data.universityInfo.rate.third">{{data.universityInfo.rate.third.toString().includes('-') ? '-' : (data.universityInfo.rate.third * 100).toFixed(2)+ '%' }}</van-col>
      <van-col span="24" class="info-item" v-if="data.universityInfo.rate.forth">{{data.universityInfo.rate.forth.toString().includes('-') ? '-' : (data.universityInfo.rate.forth * 100).toFixed(2)+ '%' }}</van-col>

      <van-col span="24" class="info-item item-tip"></van-col>
      <van-col span="24" class="info-item item-tip"> 
      </van-col>
      <van-col span="24" class="info-item item-tip"> -
      </van-col>
    </van-row>

    <wap-subtitle subtitle="" style="margin-top: 20px;">
      <template>
        <el-select v-model="levelSelected" size="small" class="select-style">
          <el-option v-for="item in levelOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </template>
    </wap-subtitle>

    <van-row :gutter="20" class="info">
      <van-col :span="24">
        <van-col span="24" class="info-item item-tip"></van-col>
        <van-col span="24" class="info-item item-tip">=-</van-col>
        <van-col span="24" class="info-item item-tip" style="margin-bottom: 20px;">
        </van-col>
        <el-table :data="table" border size="mini" class="table">
          <el-table-column prop="year" label="" width="50" fixed></el-table-column>
          <el-table-column prop="count" label="" width="60"></el-table-column>
          <el-table-column prop="highScore" label="" width="60"></el-table-column>
          <el-table-column prop="lowScore" label="" width="60"></el-table-column>
          <el-table-column prop="scoreGap" label="" min-width="60"></el-table-column>
          <el-table-column prop="position" label="" min-width="60"></el-table-column>
        </el-table>
      </van-col>

    </van-row>

    <template v-if="GotData && chart1.length > 1">
      <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
      <wap-chart1 :chart="chart1"></wap-chart1>
    </template>

    <template v-if="GotData && chart2.length > 1">
      <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
      <wap-chart2 :chart="chart2"></wap-chart2>
    </template>

    <!--  -->
    <template v-if="GotData && chart3.length > 1">
      <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
      <wap-chart3 :chart="chart3"></wap-chart3>
    </template>

    <template v-if="currentTable.length !== 0">
      <wap-subtitle subtitle="" style="margin-top: 20px;">
        <template>
          <el-select v-model="selected" size="small" class="select-style">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </template>
      </wap-subtitle>

      <van-row :gutter="20" class="info">
        <van-col span="24" class="info-item item-tip"></van-col>
        <van-col span="24" class="info-item item-tip"> =-
        </van-col>
        <van-col span="24" class="info-item item-tip"> +
        </van-col>
        <van-col span="24" class="info-item item-tip" style="margin-bottom: 20px;"> -
        </van-col>
        <van-col :span="24">
          <el-table :data="currentTable" border size="mini" class="table">
            <el-table-column prop="major" label="" min-width="60" fixed></el-table-column>
            <el-table-column prop="batch" label="" width="60"></el-table-column>
            <el-table-column prop="probability" label="" width="60">
              <template slot-scope="scope">
                {{scope.row.probability !== '-' ? (scope.row.probability * 100).toFixed(0)+'%' : '-' }}
              </template>
            </el-table-column>
            <el-table-column prop="count" label="" width="60"></el-table-column>
            <el-table-column prop="scoreGap" label="" min-width="60"></el-table-column>
            <el-table-column prop="lowScore" label="" min-width="60"></el-table-column>
            <el-table-column prop="position" label="" min-width="60"></el-table-column>
          </el-table>
        </van-col>
      </van-row>
    </template>

    <template v-if="data.chart4.length !== 0 || data.chart5.length !== 0 || data.chart6.length !== 0">
      <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
      <van-row>
        <van-col span="24" class="chart-tips">
          
        </van-col>
        <van-col span="24" class="chart-tips">
          =-20182017
        </van-col>
      </van-row>
    </template>

    <wap-chart-course-score class="chart-position" year="2017" v-if="GotData && data.chart4.length !== 0" :chart="data.chart4"></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" year="2016" v-if="GotData && data.chart5.length !== 0" :chart="data.chart5"></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" year="2015" v-if="GotData && data.chart6.length !== 0" :chart="data.chart6"></wap-chart-course-score>
    <van-row :gutter="20">
      <van-col span="24" class="tip-red">
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      level: '',
      data: {
        universityInfo: {},
        chart1: [],
        chart2: [],
        chart3: [],
        chart4: [],
        chart5: [],
        chart6: [],
        table: [],
        table2017: [],
        table2016: [],
        table2015: [],
        tableFirst: [],
        tableSecond: [],
        tableThrid: [],
        tableForth: []
      },
      currentTable: [],
      options: [
        {
          value: 'table2017',
          label: '2017'
        },
        {
          value: 'table2016',
          label: '2016'
        },
        {
          value: 'table2015',
          label: '2015'
        }
      ],
      levelOptions: [
        {
          value: 'tableFirst',
          label: ''
        },
        {
          value: 'tableSecond',
          label: ''
        },
        {
          value: 'tableThrid',
          label: ''
        },
        {
          value: 'tableForth',
          label: ''
        }
      ],
      selected: 'table2017',
      levelSelected: '',
      GotData: false,
      chart1: [],
      chart2: [],
      chart3: [],
      table: []
    }
  },
  methods: {
    initChartData(data) {
      this.chart1 = []
      this.chart2 = []
      this.chart3 = []
      const vm = this

      data.map(item => {
        if (item.highScore !== '-' && item.lowScore !== '-') {
          vm.chart1.push({
            year: item.year,
            highScore: item.highScore,
            lowScore: item.lowScore
          })
        }

        if (item.scoreGap !== '-') {
          vm.chart2.push({
            year: item.year,
            scoreGap: item.scoreGap
          })
        }

        if (item.position !== '-') {
          vm.chart3.push({
            year: item.year,
            position: item.position
          })
        }
      })
    },
    initTableData() {
      const vm = this
      const table1 = []
      this.data.tableFirst.map((item, index) => {
        if (Object.keys(item).length !== 0) {
          table1.push(item)
        }
      })
      this.data.tableFirst = table1

      const table2 = []
      this.data.tableSecond.map((item, index) => {
        if (Object.keys(item).length !== 0) {
          table2.push(item)
        }
      })
      this.data.tableSecond = table2

      const table3 = []
      this.data.tableThrid.map((item, index) => {
        if (Object.keys(item).length !== 0) {
          table3.push(item)
        }
      })
      this.data.tableThrid = table3

      const table4 = []
      this.data.tableForth.map((item, index) => {
        if (Object.keys(item).length !== 0) {
          table4.push(item)
        }
      })
      this.data.tableForth = table4
    },
    fetchData() {
      const vm = this
      const param = {
        wx_id: this.$store.state.userinfo.openid,
        categoryCode: this.$route.params.categoryCode,
        userTestId: this.$route.query.userTestId,
        testConfigType: this.$route.query.testConfigType,
        universityName: this.$route.query.universityName
      }
      this.$store.commit(this.$types.ShowLoading, true)
      this.$api.GetReportById(param).then(res => {
        if (res.data.code == '200') {
          vm.data = Object.assign({}, res.data.data)
          vm.initTableData()
          vm.GotData = true

          const options = []
          vm.options.map((item, index) => {
            if (vm.data[item.value] && vm.data[item.value].length !== 0) {
              options.push(item)
            }
          })
          vm.options = options
          vm.currentTable =
            vm.options.length !== 0 ? vm.data[vm.options[0].value] : []

          const levelOptions = []
          vm.levelOptions.map((item, index) => {
            if (vm.data[item.value] && vm.data[item.value].length !== 0) {
              levelOptions.push(item)
            }
          })

          vm.levelOptions = levelOptions

          vm.levelSelected = vm.levelOptions[0] ? vm.levelOptions[0].value : ''
        } else {
          vm.$dialog
            .alert({
              message: res.data.message
            })
            .then(() => {
              vm.$router.push({ name: 'Test' })
            })
        }
        vm.$store.commit(vm.$types.ShowLoading, false)
      })
    }
  },
  watch: {
    selected: {
      handler(val) {
        if (this.data[val] && this.data[val].length !== 0) {
          this.currentTable = this.data[val]
        }
      },
      immediate: true
    },
    levelSelected: {
      handler(val) {
        if (val !== '' && this.data[val] && this.data[val].length !== 0) {
          this.initChartData(this.data[val])
          this.table = this.data[val]
        }
      },
      immediate: true
    }
  },
  created() {
    this.fetchData()
    window.scrollTo(0, 0)
  }
}
</script>


<style lang="scss" scoped>
.info {
  font-size: 13px;
  padding-top: 10px;
  .info-item {
    padding-top: 5px;
    .item-red {
      color: #cc0000;
    }

    .item-blue {
      color: #0066ff;
    }

    &.item-tip {
      color: #cc0000;
      font-size: 12px;
    }
  }

  .rate {
    display: inline-block;
    position: relative;
  }
}

.chart-item {
  margin-top: 20px;
}

.chart-position {
  bottom: -20px;
}

.tip-red {
  color: #cc0000;
  font-size: 12px;
}

.chart-tips {
  color: #cc0000;
  font-size: 12px;
  padding: 10px 5px;
}

.select-style {
  position: absolute;
  width: 120px;
  right: 20px;
  top: -5px;
}
</style>



